:root {
  --font-family: Segoe UI, SegoeUI, "Roboto";
  --dark-blue: #02022D;
  --red: #FD4303;
  --orange: #FD7403;
  --blue: #157DD1;
  --light-blue: #01DAFE;
  --light-grey: #F4F4F4;
  --grey: #707070;
  --dark-grey: #707070;
  --white: #fff;
  --video-wrapper-bg: #171717;
  --video-room-bg: #212121;
  --main-color: #2fe08f;
  --background-white-gradient: transparent linear-gradient(171deg, #FFFFFF 0%, #E8E8E8 100%) 0% 0% no-repeat padding-box;
  --box-shadow-light: 0px 1px 4px rgba(0,0,0,0.16);
}